body {
  @apply bg-dark-purple bg-no-repeat bg-contain text-white;
  background-position: bottom;
  background-position-y: 140%;
}

#login {
  width: 400px;
}
#login #login_error {
  color: #000;
}
#login .message {
  color: #000;
}
#login h1 {
  @apply mb-12;
}
#login h1 a {
  width: 200px;
  height: 65.1px;
  background-size: 200px 65.1px;
  @apply mx-auto mb-0;
}
#login h1 a:focus {
  box-shadow: none;
}
#login form {
  @apply shadow-none p-10 border-white/0 bg-white uppercase;
}
#login form label {
  @apply text-sm text-dark-purple mb-2 font-condensed font-semibold;
}
#login form .input {
  @apply w-full border-2 border-white border-b-light-grey py-3 px-2 font-sans text-sm text-dark-purple transition-all duration-500 ease-in-out mx-0;
}
#login form .input::placeholder {
  @apply font-sans text-gray-300 text-base;
}
#login form .input:focus {
  outline: none;
  @apply border-b-red shadow-none bg-light-grey;
}
#login form .input:focus-visible {
  outline: none;
}
#login form input[type=checkbox] {
  /* Add if not using autoprefixer */
  -webkit-appearance: none;
  appearance: none;
  /* For iOS < 15 to remove gradient background */
  background-color: #fff;
  /* Not removed via appearance */
  margin: 0;
}
#login form input[type=submit] {
  @apply font-condensed font-bold text-white text-sm transition-all duration-500 ease-in-out inline-flex justify-center items-center bg-red uppercase py-2 px-6 border border-red rounded-none mt-8 hover:text-white hover:bg-dark-red tracking-[1px];
}
#login #backtoblog, #login #nav {
  @apply font-sans font-light text-center text-white;
}
#login #backtoblog a, #login #nav a {
  @apply font-sans font-light text-white;
}
#login input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: #000 !important;
}

.wpml-login-ls form#wpml-login-ls-form select#wpml-language-switcher-locales {
  @apply rounded-none border border-solid uppercase text-xs font-sans font-semibold transition-all duration-300 ease-in hover:bg-white;
}
.wpml-login-ls form#wpml-login-ls-form input.button {
  @apply rounded-none border border-solid uppercase text-xs font-sans font-semibold px-4 py-1 transition-all duration-300 ease-in hover:bg-white;
}